.cards_items{
    padding:20px 25px;
    text-transform: none;
    text-decoration: none;
    color: white;
    list-style-type: none;
    width: 100%;
    height: 170px;
    margin-bottom: 50px;
    font-family: 'Roboto', sans-serif;
    background-color: transparent;
    cursor: pointer;
    box-shadow: 0 6px 20px rgba(24, 61, 129, 0.1);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(6, 17, 40, 0.17));

}


.cards__link
{
    display: flex;
    align-self: start;
    align-items:start;
    justify-content: center;
    flex-direction: row;
    text-decoration: none;

    transition: all 0.2s linear;
}

.cards__link figure{
    height:fit-content;
    width: 200px;
}
.cards__link figure img{
    height: 100px;
    width: 100px;
}

.cards__link div{
    margin-left: 10px;
}
.cards__link div h5{
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 700;
    color: #49d3d9;
    font-family: 'Belanosima', sans-serif;
}

.cards__link div p{
    margin-top: 5px;
    color: rgba(255, 255, 255, 0.75);
    font-size: 16px;
    font-weight: 100;
}

.cards__link .cards__read{
    position: absolute;
    font-size: 14px;
    font-weight: 200;
    bottom: 15px;
    right: 15px;
    color: #49d9d23e;
}
.cards__link .cards__read:hover{
    color: #49d9d3;
}
@media screen and (max-width: 960px) {
    .cards{
        width: 100%;
        margin-bottom: 25px;

    }
}